<template>
  <div class="container my-5 px-4 px-lg-5">
    <div class="row gx-4 gx-lg-5 justify-content-center">
      <div class="col-md-10 col-lg-8 col-xl-7">
        <h1>회원가입</h1>
        <div class="my-5">
          <form id="userform" method="post" action="">
            <div class="text-start">
              <label for="name">이름</label>
              <input
                class="form-control"
                id="name"
                type="text"
                v-model="name"
                placeholder="이름을 입력해주세요."
              />
            </div>
            <div class="text-start">
              <label for="id">ID</label>
              <input
                class="form-control"
                id="id"
                type="text"
                v-model="id"
                placeholder="ID를 입력해주세요."
              />
              <div id="idresult" class="mt-1"></div>
            </div>
            <div class="text-start">
              <label for="pass">Password</label>
              <input
                class="form-control"
                id="pass"
                type="password"
                v-model="pass"
                placeholder="비밀번호를 입력해주세요."
              />
            </div>
            <div class="text-start">
              <label for="pass">Password Check</label>
              <input
                class="form-control"
                id="passcheck"
                type="password"
                v-model="passcheck"
                placeholder="비밀번호를 다시 입력해주세요."
              />
            </div>
            <!-- email 주소 -->
            <div class="text-start">
              <p class="mt-3 mb-0">(선택정보)</p>
              <label for="email">E-mail</label>
              <input
                class="form-control"
                id="email"
                type="e-mail"
                v-model="email"
                placeholder="e-mail을 입력해주세요.(example: -----@-----.--- 형식으로 입력)"
              />
            </div>
            <!-- 관심장르 -->
            <div class="text-start">
              <label for="genre">관심 장르</label>
              <input
                class="form-control"
                id="genre"
                type="text"
                v-model="genre"
                placeholder="관심 장르를 입력해주세요."
              />
            </div>
            <!-- 목표 -->
             <div class="text-start">
              <label for="goal">목표</label>
              <input
                class="form-control"
                id="goal"
                type="text"
                v-model="goal"
                placeholder="목표를 입력해주세요."
              />
            </div>           
            <!-- 소셜 링크 -->
             <div class="text-start">
              <label for="socialLink">SNS 링크</label>
              <input
                class="form-control"
                id="socialLink"
                type="url"
                v-model="socialLink"
                placeholder="소셜링크를 입력해주세요.(example: https://----.--- 형식으로 입력)"
              />
            </div>    
            <br />
            <!-- Submit Button-->
            <b-button variant="primary" @click="checkValue"
              >Sign Up</b-button
            >&nbsp;
            <b-button variant="danger" @click="resetValue"
              >Cancel</b-button
            >
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/config/http-common.js";

export default {
  name: "Signup",
  data() {
    return {
      name: "",
      id: "",
      pass: "",
      passcheck: "",
      email: "",
      genre: null,
      goal: null,
      socialLink: ""
    };
  },

  methods: {
    // 입력값 체크하기 - 체크가 성공하면 registBook 호출
    checkValue() {
      // 사용자 입력값 체크하기
      // isbn, 제목, 저자, 가격, 설명이 없을 경우 각 항목에 맞는 메세지를 출력
      let err = true;
      let msg = "";
      !this.name &&
        ((msg = "이름을 확인해주세요."),
        (err = false)) /*, this.$refs.name.focus()*/;
      err && !this.id && ((msg = "ID를 확인해주세요."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        !this.pass &&
        ((msg = "비밀번호를 확인해주세요."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        this.pass != this.passcheck &&
        ((msg = "비밀번호를 다시 확인해주세요"), (err = false));
      if (!err) alert(msg);
      else this.registMember();
    },  

    // 일단 회원가입할 때 독서 마라톤을 생성한다
    createMarathon(id) {
      http
        .post(`/marathon/create`, {
          userId: id,
          goalPages: 1 // 기본적으로 1으로 생성
        })
        .then(({ data }) => {
          // 서버에서 정상적인 값이 넘어 왔을경우 실행.
          console.log(data)
        })
        .catch((err) => {
          console.log(err.response)
          alert("독서마라톤 생성에 실패했습니다. 관리자에게 문의하세요.")
        })
    },
    registMember() {
      http
        .post(`/users`, {
          userName: this.name,
          userId: this.id,
          password: this.pass,
          email: this.email,
          genre: this.genre,
          goal: this.goal,
          socialLink: this.socialLink
        })
        .then(({ data }) => {
          let msg = "등록에 문제가 생겼습니다.";
          // 서버에서 정상적인 값이 넘어 왔을경우 실행.
          if (data === "success") {
            msg = "회원가입을 축하합니다.";
            // 독서마라톤 생성
            this.createMarathon(this.id)
            alert(msg);
            this.$router.push({ name: "Home" });
          }
        })
        .catch((err) => {
          if (err.response.data === "이미 존재하는 사용자 ID입니다.") {
            alert(err.response.data)
          }
        })
    },
    resetValue() {
      this.$router.push("/");
    },
  },
};
</script>

